import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { HomeGuard } from '../guards/home.guard';
import { HomePage } from './home.page';
import { UserDataResolver } from '../resolvers/user-data.resolver';

const routes: Routes = [
  {
    path: 'home',
    component: HomePage,
    canActivate: [HomeGuard],
    resolve: {
      userData: UserDataResolver
    },
    children:[
      {
        path: 'welcome',
        loadChildren: () => import('./welcome/welcome.module').then( m => m.WelcomePageModule)
      },
      {
        path: 'country-list',
        loadChildren: () => import('./country-list/country-list.module').then( m => m.CountryListPageModule)
      },
      {
        path: 'user-profile',
        loadChildren: () => import('./user-profile/user-profile.module').then( m => m.UserProfilePageModule)
      },
      {
        path: 'application-center',
        loadChildren: () => import('./application-center/application-center.module').then( m => m.ApplicationCenterPageModule)
      },
      {
        path: 'universities',
        loadChildren: () => import('./universities/universities.module').then( m => m.UniversitiesPageModule)
      },
      {
        path: 'universities/:countryID',
        loadChildren: () => import('./universities/universities.module').then( m => m.UniversitiesPageModule)
      },      
      {
        path: 'landing-page',
        loadChildren: () => import('./landing-page/landing-page.module').then( m => m.LandingPagePageModule)
      },
      {
        path: 'top-links',
        loadChildren: () => import('./top-links/top-links.module').then( m => m.TopLinksPageModule)
      },
      {
        path: 'study-germany',
        loadChildren: () => import('./study-germany/study-germany.module').then( m => m.StudyGermanyPageModule)
      },
      {
        path: 'other-pages',
        loadChildren: () => import('./other-pages/other-pages.module').then( m => m.OtherPagesPageModule)
      },
      {
        path: 'counselling',
        loadChildren: () => import('./counselling/counselling.module').then( m => m.CounsellingPageModule)
      },
      {
        path: 'college-predictor',
        loadChildren: () => import('./college-predictor/college-predictor.module').then( m => m.CollegePredictorPageModule)
      },
      {
        path: 'scholarship',
        loadChildren: () => import('./scholarship/scholarship.module').then( m => m.ScholarshipPageModule)
      },
      {
        path: 'online-coaching',
        loadChildren: () => import('./online-coaching/online-coaching.module').then( m => m.OnlineCoachingPageModule)
      },
      {
        path: 'blog',
        loadChildren: () => import('./blog/blog.module').then( m => m.BlogPageModule)
      }
    ]
  },
  {
    path:'',
    redirectTo:'/login-new',
    pathMatch: 'full'
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class HomePageRoutingModule {}
